import React, { useState } from "react"
import { InputGroup, FormControl, Form, Button, FloatingLabel, Row, Col } from "react-bootstrap"


const NumberConverter = () => {

    const [number, setNumber] = useState("")
    const [baseGiven, setBaseGiven] = useState("10")
    const [baseWanted, setBaseWanted] = useState("2")
    const [calcSteps, setCalcSteps] = useState("")

    const btnDisabled = !number || !baseGiven || !baseWanted

    const getDigits = (base) => {
        return base === "16"
            ? [...Array(10).keys(), ...["a", "b", "c", "d", "e", "f"]].map(x => x.toString())
            : [...Array(parseInt(base)).keys()].map(x => x.toString())
    }

    const onChangeGivenNumber = (event) => {
        const { value } = event.target

        let digits = getDigits(baseGiven)
        let valid = validateNumber(value, digits)


        if (valid) setNumber(value)

    }

    const onChangeGivenBase = (event) => {
        const { value } = event.target
        let digits = getDigits(value)
        setBaseGiven(value)

        if (!validateNumber(number, digits)) setNumber("0");
    }

    const validateNumber = (number, digits) => {
        return [...number].every(item => digits.includes(item.toString()))
    }

    const calculateAny = () => {
        let deziNumber = 0
        let steps = ""
        if (baseGiven !== "10") {
            [...number].reverse().forEach((digit, i) => {
                let convertedDigit
                switch (digit) {
                    case "a":
                        convertedDigit = 10
                        break
                    case "b":
                        convertedDigit = 11
                        break
                    case "c":
                        convertedDigit = 12
                        break
                    case "d":
                        convertedDigit = 13
                        break
                    case "e":
                        convertedDigit = 14
                        break
                    case "f":
                        convertedDigit = 15
                        break
                    default:
                        convertedDigit = parseInt(digit)
                }
                let deziDigits = convertedDigit * Math.pow(parseInt(baseGiven), i)
                steps = steps + `${convertedDigit} * ${baseGiven} ^ ${i} = ${deziDigits}\n`
                deziNumber += deziDigits

            })
            steps = steps + `${number} to the base ${baseGiven} is ${deziNumber} in dezimal \n\n\n`

            calculate(deziNumber, steps)
        } else {
            calculate(number)
        }
    }

    const calculate = (dezimalNumber, preSteps) => {
        let digits = [];
        let n = dezimalNumber;
        let b = parseInt(baseWanted)
        let steps = preSteps || ""

        while (true) {
            let oldN = n
            let r = n % b;
            n = Math.floor(n / b);
            let convertedR
            switch (r) {
                case 10:
                    convertedR = 'a'
                    break
                case 11:
                    convertedR = 'b'
                    break
                case 12:
                    convertedR = 'c'
                    break
                case 13:
                    convertedR = 'd'
                    break
                case 14:
                    convertedR = 'e'
                    break
                case 15:
                    convertedR = 'f'
                    break
                default:
                    convertedR = r

            }

            digits.push(convertedR);
            steps = steps + `dividing ${oldN} by ${b} gives ${n} rest ${convertedR}\n`
            if (n === 0) break;
        }

        digits.reverse();

        let convertedNumber = digits.join('');

        steps = steps + `The converted number is ${convertedNumber} to the base ${baseWanted}`

        setCalcSteps(steps)
    }

    const dynHeight = [...calcSteps].reduce((a, v) => v === "\n" ? a + 1 : a, 0) * 30

    return (
        <>
            <Row className="g-2">
                <Col md>


                    <FloatingLabel controlId="floatingSelect1" label="Given Base">
                        <Form.Select value={baseGiven} onChange={onChangeGivenBase}>
                            <option value="2">2</option>
                            <option value="8">8</option>
                            <option value="16">16</option>
                            <option value="10">10</option>
                        </Form.Select>
                    </FloatingLabel>
                </Col>
                <Col>
                    <FormControl value={number} onChange={onChangeGivenNumber} placeholder="enter number" disabled={!baseGiven} />
                </Col>
                <Col>

                    <FloatingLabel controlId="floatingSelect2" label="Wanted Base">
                        <Form.Select value={baseWanted} onChange={(event) => setBaseWanted(event.target.value)}>
                            <option value="2">2</option>
                            <option value="8">8</option>
                            <option value="16">16</option>
                            <option value="10">10</option>
                        </Form.Select>
                    </FloatingLabel>
                </Col>
            </Row>
            <div style={{textAlign: 'center'}}><Button disabled={btnDisabled} onClick={calculateAny}>Calculate</Button></div>
            

            <Form.Control
                disabled
                as="textarea"
                placeholder="Click calculate button to show calculation steps..."
                value={calcSteps}
                style={{ height: dynHeight + 'px' }}
            />

        </>
    )
}

export default NumberConverter